<template>
  <div class="bloghome">
    <div class="blogsearch">
      <div class="blogcont">
        <input type="text" v-model="searchcont">
        <button v-on:click.prevent="searchs">搜索</button>
      </div>
    </div>
    <ul>
      <li v-for="blogArrays in searchs" :key="blogArrays.id">
        <div>
          <h6>
            <router-link v-bind:to="'/blogdetails/'+blogArrays.id">{{blogArrays.title}}</router-link>
          </h6>
          <p v-changecolor>{{blogArrays.cont |fontsizes}}</p>
          <p>
            标签:
            <span
              v-for="item in blogArrays.bqarray"
              :key="item"
              style="margin-right:10px;"
            >{{item}}</span>
          </p>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "bloghome",
  data() {
    return {
      blogArray: [],
      searchcont: ""
    };
  },

  created() {
    axios.get("/api/posts.json").then(data => {
      var arrays = [];
      for (let key in data.data) {
        console.log(key);
        data.data[key].id = key;
        arrays.push(data.data[key]);
      }
      this.blogArray = arrays;
      //console.log(arrays);
      // console.log(this.blogArray);
    });
  },
  computed: {
    searchs() {
      return this.blogArray.filter(blog => {
        return blog.title.match(this.searchcont);
      });
    }
  },
  filters: {
    fontsizes(value) {
      return value.slice(0, 100) + "...";
    }
  },
  directives: {
    changecolor: {
      bind(el, binding, vnode) {
        el.style.color =
          "#" +
          Math.random()
            .toString(16)
            .slice(2, 8);
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
* {
  padding: 0;
  margin: 0;
}
.bloghome {
  width: 800px;
  margin: 0 auto;
  margin-top: 20px;
}
.bloghome ul li {
  list-style: none;
  margin-top: 20px;
  background: #eee;
  padding: 10px;
  border-radius: 5px;
}
.bloghome ul li h6 {
  font-size: 14px;
}

.blogsearch {
  list-style: none;
  margin-top: 20px;
  background: #eee;
  padding: 10px;
  border-radius: 5px;
  margin: 0 auto;
  text-align: center;
  overflow: hidden;
}
.blogsearch input {
  display: inline-block;
  width: 500px;
  float: left;
  margin: 0 auto;
  height: 30px;
  line-height: 50px;
  border: 1px solid #999;
  border-radius: 5px;
  padding: 0 5px;
}
.blogsearch button {
  display: inline-block;
  float: left;
  width: 100px;
  height: 30px;
  border: 1px solid #999;
  border-radius: 5px;
  padding: 0 5px;
}
.blogcont {
  width: 640px;
  margin: 0 auto;
}
</style>



