<template>
  <div class="blogdetails">
    <ul>
      <li>
        <div>
          <h6>{{blogArrays.title}}</h6>
          <p>{{blogArrays.cont}}</p>
          <p>
            标签:
            <span
              v-for="item in blogArrays.bqarray"
              :key="item"
              style="margin-right:10px;"
            >{{item}}</span>
          </p>
          <p>
            作者：
            <span
              v-for="item in blogArrays.checkboxs"
              :key="item"
              style="margin-right:10px;"
            >{{item}}</span>
          </p>
        </div>
      </li>
      <router-link :to="'/blogedittwo/' +id">修改编辑</router-link>
      <span v-on:click="dete">删除</span>
    </ul>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "blogdetails",
  data() {
    return {
      id: this.$route.params.id,
      blogArrays: {}
    };
  },
  methods: {
    dete: function() {
      axios.delete("/api/posts/" + this.id + ".json").then(data => {
        this.$router.push({ path: "/" });
      });
    }
  },
  created() {
    axios.get("/api/posts/" + this.id + ".json").then(data => {
      console.log(data);
      this.blogArrays = data.data;
    });
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.blogdetails {
  width: 820px;
  margin: 0 auto;
  background: #eee;
  padding: 10px;
}
.blogdetails ul li {
  border-radius: 5px;
  margin-top: 20px;
}
.blogdetails ul li h6 {
  font-size: 14px;
}
</style>



