<template>
  <div class="blogedit">
    <div class="blogedit-cont" v-if="!messageshow">
      <ul>
        <li>
          <label for>标题:</label>
          <input type="text" v-model="blog.title">
        </li>
        <li>
          <label for>博客内容:</label>
          <textarea v-model="blog.cont"></textarea>
        </li>
        <li class="iptstyle">
          <input type="checkbox" v-model="blog.bqarray" name="sqw" value="抒情文"> 抒情文
          <input type="checkbox" v-model="blog.bqarray" name="ssw" value="散诗文"> 散诗文
          <input type="checkbox" v-model="blog.bqarray" name="tyw" value="田园文"> 田园文
          <input type="checkbox" v-model="blog.bqarray" name="cgw" value="长歌文"> 长歌文
        </li>
        <li>
          <select name id v-model="blog.checkboxs">
            <option value="cy">cy</option>
            <option value="whl">whl</option>
            <option value="czj">czj</option>
          </select>
        </li>
        <li>
          <button class="btncs" v-on:click.prevent="save">保存</button>
        </li>
      </ul>
    </div>

    <!--数据显示-->
    <div class="datas" v-show="messageshow">
      <h1>当前信息</h1>
      <ul>
        <li>标题:{{blog.title}}</li>
        <li>内容:{{blog.cont}}</li>
        <li>
          标签:
          <span v-for="bqarrays in blog.bqarray" :key="bqarrays">{{bqarrays}}&nbsp;&nbsp;</span>
        </li>
        <li>作者:{{blog.checkboxs}}</li>
      </ul>
      <div class="bjedit">
        <span v-on:click.prevent="jxedit">
          <span v-on:click.prevent="jxbj" class="jxbjed">继续编辑</span>
        </span>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "blogedit",
  data() {
    return {
      id: this.$route.params.id,
      blog: {},

      messageshow: false
    };
  },
  created() {
    console.log(this.id);
    this.result();
  },
  methods: {
    result() {
      axios.get("/api/posts/" + this.id + ".json").then(data => {
        console.log(data);
        this.blog = data.data;
        console.log(data);
        console.log("cy");
        console.log(this.blog);
      });
    },
    save: function() {
      axios.post("/api/posts.json", this.blog).then(data => {
        //console.log(data);
        this.messageshow = true;
      });
    },
    jxedit: function() {
      this.messageshow = false;
    },
    jxbj: function() {
      this.blog.title = "";
      this.blog.cont = "";
      this.blog.bqarray = [];
      this.blog.checkboxs = "";
      this.$router.push("/blogedit");
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.datas {
  width: 798px;
  margin: 0 auto;
  border: 1px solid #999;
  margin-top: 20px;
}
.bjedit {
  text-indent: 20px;
  margin: 20px 0;
}
.jxbjed:hover {
  cursor: pointer;
}
.jxbjed {
  color: #f00;
}
.datas h1 {
  font-size: 20px;
  text-indent: 20px;
}
.datas li {
  list-style: none;
  line-height: 30px;
}
.blogedit-cont {
  width: 800px;
  margin: 0 auto;
}
.blogedit-cont ul li {
  list-style: none;
}
label {
  display: block;
  margin-top: 10px;
}
input {
  display: inline-block;
  width: 100%;
  border-radius: 5px;
  height: 35px;
  border: 1px solid #999;
  margin-top: 10px;
}
textarea {
  width: 100%;
  height: 160px;
  border: 1px solid #999;
  margin-top: 10px;
  border-radius: 5px;
}
.iptstyle input[type="checkbox"] {
  display: inline;
  width: 15px;
  height: 15px;
  margin-top: 10px;
}
select {
  width: 50%;
  height: 35px;
  margin-top: 10px;
}
.btncs {
  width: 30%;
  height: 35px;
  background: #7f68f1;
  border-radius: 5px;
  border: none;
  color: #fff;
  margin-top: 10px;
}
.header-cont a.router-link-exact-active {
  background: #000;
}
</style>



